.wrapHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.link {
    text-decoration: none;
  }

  .rightMenu {
    display: flex;
    align-items: center;
    gap: 2px;

  }

  .burger {
    position: fixed;
    top: -28px;
    right: -268px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: 280px;
    width: 65%;
    max-width: 218px;
    height: 110vh;
    /* background-color: #fff; */
    transition: transform 0.3s linear;
    border: 1px solid red;
    padding: 20px 20px 40px 20px ;
    
    /* z-index: 5; */
  }

  .active {
    transform: translateX(-80%);
    position: absolute;
  }

  .burgerBtn {
    display: block;
    margin-left: auto;
    background-color: transparent;
    padding: 0;
    border: 0;
    cursor: pointer;
  }

  .btnClose {
    position: absolute;
    top: 28px;
    right: 20px;
    background-color: transparent;
    padding: 0;
    border: none;
}

.textLogo {
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: -0.04em;
}

.navList {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.navItem {
    border: 1px solid ;
    border-radius: 30px;
    padding: 0px 15px;
    width: 119px;
    height: 48px;
font-size: 14px;
line-height: 129%;
letter-spacing: -0.03em;
display: flex;
justify-content: center;
align-items: center;

}

.navLink {
    text-decoration: none;
    cursor: pointer;
    /* color: #262626; */

}

  .authNav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  .linkLogin {
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
padding: 0px 12px;
width: 100%;
max-width: 178px;
height: 42px;
background: #f6b83d;
font-weight: 700;
font-size: 14px;
line-height: 129%;
letter-spacing: -0.03em;
text-transform: uppercase;
color: #fff;
text-decoration: none;
  }

  .linkReg {
    border-radius: 30px;
padding: 0px 12px;
width: 100%;
height: 42px;
background: #fff4df;
font-family: var(--font-family);
font-weight: 700;
font-size: 14px;
line-height: 129%;
letter-spacing: -0.03em;
text-transform: uppercase;
color: #f6b83d;
text-decoration: none;
display: flex;
    justify-content: center;
    align-items: center;
  }